// import axios from '../axios'
import axios from 'axios'

/* 
 * 用户管理模块
 */

// 保存
export const save = (data) => {
    return axios({
        url: '/user/save',
        method: 'post',
        data
    })
}
// 删除
export const batchDelete = (data) => {
    return axios({
        url: '/user/delete',
        method: 'post',
        data
    })
}
// 分页查询
export const findPageProcess = (data) => {
    return axios({
        url: 'static/ConfigurationManaList.json',
        method: 'get',
        data
    })
    // return axios.get('/xaj/configuration/searchProductionProcesses',{
    //     params:data
    // })
}
